@import "./../base/vars";

.breadcrumb {
  height: 112px;
  @media (max-width: $lg) {
    height: 80px;
  }
  @media (max-width: $sm) {
    display: none;
  }
  &__list {
    height: 100%;
    display: flex;
    align-items: center;

    li:first-child > a {
      padding-left: 0;
    }
    li:not(:last-child) > a {
      border-right: 1px dotted rgba(63, 78, 101, 0.3);
    }
    li:last-child > a {
      opacity: 50%;
    }
  }
  &__link {
    padding: 5px 10px;
    font-weight: 300;
    color: $darkBlue;
    white-space: nowrap;
    &:hover {
      color: $yellow;
    }
  }
}
